<template>
    <div class="toolbar">
        <el-pagination
                background
                :current-page="$store.state.pageInfo.pageIndex"
                @current-change="changePageIndex"
                layout="prev, pager, next"
                :page-count="$store.state.pageInfo.pageSum">
        </el-pagination>
    </div>
</template>

<script>
    export default {
        name: "page-info",
        props:["actionsName","params"],
        methods:{
            // 当你的页数发生变化时
            changePageIndex(pageIndex){
                const sendParams={
                    pageIndex
                };
                if(this.params){
                    Object.assign(sendParams,this.params)
                }
                // console.log(111111,this.actionsName);
                this.$store.dispatch(this.actionsName,sendParams)
            }
        },
        mounted(){
            // 将数据初如化
            // console.log(22222);
            this.changePageIndex(1);
            //  this.$store.dispatch(this.actionsName,{pageIndex:1})
        }
    }
</script>

